.programms__inner::before {
  content: "";
  display: block;
  position: absolute;
  background-image: url("../img/programm-sketch_bg-d.png");
  background-repeat: no-repeat;
  width: 680px;
  height: 426px;
  bottom: 653px;
  right: 0;
}


.programms h2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 49px;
  text-align: start;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: $header-color;
  margin-top: 16px;
  padding-left: 104px;
  margin-bottom: 69px;
}

.programms__wrapper {
  display: flex;
  padding: 0 104px;
}

.programms__tabs {
  width: 93%;
}

.programms__tabs-list {
  @include list-style;
}

.programms__tabs-btn {
  color: $color-default-black;
  font-size: 15px;
  line-height: 18px;
  opacity: 0.87;
  margin: 16px 0;
  padding: 10px 15px;

  &--current {
    color: $color-default-white;
  }
}

