.programms__list {
  @include list-style;
  display: flex;
  justify-content: space-evenly;
}

.programms__item {
  display: none;

  &--current {
    display: block;
    width: 83%;

    &:nth-child(1) {
      width: 78%;
    }

    &:nth-child(2) {
      margin-left: -107px;
      width: 85%;
    }

    &:nth-child(3) {
      width: 86%;
    }

    &:nth-child(4) {
      width: 81%;
    }

    &:nth-child(5) {
      width: 77%;
    }
  }
}

.programms__tabs-item span {
  margin-left: 25px;
}

.programms__tabs-item--current span {
  margin-left: 19px;
}

