.programms__tabs-item {
  width: 205px;


  &--current {
    background: $arrow-gradient;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 6px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(50%, -50%) rotate(45deg);
    border: 47px solid $arrow-yellow;
    border-left: none;
    border-bottom: none;
    z-index: -1;
  }
}


